export default {
	data() {
		return {
			user:false,
			authKey:'',
			dot:'',
			userInfo:''
		};
	},
	onLoad(e) {
		this.authKey = uni.getStorageSync('authKey')
		if (this.authKey){
			this.checkSession()
		}
		this.userInfo = uni.getStorageSync('userInfo');
	},
	onShow() {
		this.getdot();
	},
	methods:{
		// 检查会话是否失效
	checkSession: function() {
			var that = this;
			uni.request({
				url: this.http + 'user/checkSession',
				method: 'POST',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					if (res.data.code == 101) {
						that.user = true
						that.authKey = ''
						uni.setStorageSync('authKey','')
					} else {
						that.user = false
					}
				},
			});
	},
	wxGetUserInfo:function(res){
		let that = this
		if (!res.detail.iv) {
			uni.showToast({ 
				title: "您取消了授权,登录失败", 
				icon: "none" ,
			}); 
			return false; 
		}
		let simpleUser = res.detail.userInfo
		let encryptedData = res.detail.encryptedData
		let iv = res.detail.iv
		uni.login({
			provider: 'weixin',
		    success(res){
			  console.log(res.code,'login')
			  var data = res.code
		    if (res.code) {
			  uni.request({
			  	url: that.http + 'user/token',
			  	method: 'GET',
			  	data: {
			  		login_code: res.code,
					encrypted_data:encryptedData,
					iv: iv
			  	},
			  	success: res => {
					if (res.data.code == 200) {
						let auth = res.data.data.authkey
						that.authKey = auth
						that.userInfo = simpleUser
						uni.setStorageSync('authKey',auth)
						uni.setStorageSync('userInfo',simpleUser)
						that.user = false
					} else {
						uni.showToast({
							title: res.data.error,
							icon: 'none'
						})
					}
			  		
			  		// that.haveOrder()
			  	},
			  });
		    } else {
		      console.log('登录失败！' + res.errMsg)
		    }
		  }
		})
	},
		close(e){
			this.user = false;
			uni.showToast({
				title: '未微信授权',
				icon:'none',
				mask: false,
				duration: 1500
			})
		},
		goActivity(){
			uni.navigateTo({
				url: '/pages/service/detail?id=28'
			})
		},
		getdot(){
			uni.request({
				url: this.http+'order/overview',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						this.dot = res.data.data
					}
				},
			});
		},
		goOrder(e){
			wx.getSetting({
				  success(res) {
				    if (res.authSetting['scope.userInfo']) {
						console.log('已经授权')
				      // 已经授权，可以直接调用 getUserInfo 获取头像昵称
				      wx.getUserInfo({
				        success(res) {
						  uni.setStorageSync('userInfo',res.userInfo)
				        }
				      });
					  uni.navigateTo({
					  	url: '/pages/order/order?tabIndex=' + e
					  });
				    }else{
						console.log('未授权')
						uni.showToast({
							title:"请微信授权",
							icon:'none',
							duration:1500
						})
					}
				  },
				  fail(res){
				    console.log(res,'登录过期了')
				    uni.showModal({
				      title: '提示',
				      content: '你的登录信息过期了，请重新登录',
				    })
					wx.login({
					    success: function (res) {
					      console.log(res.code)
					      //发送请求
					      wx.request({
					        url: 'https://zct.51kdd.com/index.php', 
					        data: {
					          code: res.code
					        },
					        header: {
					          'content-type': 'application/json' // 默认值
					        },
					        success(res) {
					          console.log(res)
					        }
					      })
					    }
					})
				}
			})
			
		},
		goDetail(e){
			var that = this
			if(e==6){
				uni.navigateTo({
					url: '/pages/mine/aboutsAs/aboutsAs'
				});
			}else{
				wx.getSetting({
					  success(res) {
					    if (res.authSetting['scope.userInfo']) {
							console.log('已经授权')
					      // 已经授权，可以直接调用 getUserInfo 获取头像昵称
					      wx.getUserInfo({
					        success(res) {
					          console.log(res.userInfo)
							  uni.setStorageSync('userInfo',res.userInfo)
					        }
					      });
						  if(e==1){
						  	uni.navigateTo({
						  		url: '/pages/mine/collection/collection'
						  	});
						  }else if(e==2){
						  	uni.navigateTo({
						  		url: '/pages/mine/myTechnician/myTechnician'
						  	});
						  }else if(e==3){
						  	uni.navigateTo({
						  		url: '/pages/mine/address/address'
						  	});
						  }else if(e==4){
						  	uni.navigateTo({
						  		url: '/pages/mine/coupons/coupons'
						  	});
						  }else if(e==5){
						  	uni.request({
						  		url: that.http+'copemate/homepage',
						  		method: 'GET',
						  		header: {
						  			authKey: that.authKey
						  		},
						  		data: {},
						  		success: res => {
						  			console.log(res.data)
						  			if (res.data.code == 200) {
						  	             var part = res.data.data.is_copemate
						  				 if(part==1){
						  					 uni.navigateTo({
						  					 	url: '/pages/mine/partnership/associate'
						  					 });
						  				 }else if(part==0){
						  					uni.navigateTo({
						  						url: '/pages/mine/partnership/partnership'
						  					}); 
						  				 }
						  			}
						  		},
						  	});
						  }
					    }else{
							console.log('未授权')
							uni.showToast({
								title:"请微信授权",
								icon:'none',
								duration:1500
							})
						}
					  },
					  fail(res){
					    console.log(res,'登录过期了')
					    uni.showModal({
					      title: '提示',
					      content: '你的登录信息过期了，请重新登录',
					    })
						wx.login({
						    success: function (res) {
						      console.log(res.code)
						      //发送请求
						      wx.request({
						        url: 'https://zct.51kdd.com/index.php', 
						        data: {
						          code: res.code
						        },
						        header: {
						          'content-type': 'application/json' // 默认值
						        },
						        success(res) {
						          console.log(res)
						        }
						      })
						    }
						})
					}
				})
			}
		}
	}
}
